.inviteFriends .main {
  flex: 1 1;
  margin: 1.5625rem;
  margin-bottom: 0.625rem;
}
.inviteFriends .main .mainTitle {
  color: #fff;
  display: flex;
  align-items: center;
  position: relative;
}
.inviteFriends .main .mainTitle span {
  font-size: 1.125rem;
}
.inviteFriends .main .mainTitle .rebate {
  width: 4.0625rem;
  font-size: 0.625rem;
  padding: 0.15625rem;
  padding-left: 0.46875rem;
  padding-right: 0.46875rem;
  margin-left: 0.46875rem;
  text-align: center;
  border-radius: 3px;
  background: linear-gradient(135deg, #fd982f, #fd7900);
}
.inviteFriends .main .mainTitle .qrCode {
  position: absolute;
  right: 0px;
  display: flex;
  justify-content: flex-start;
}
.inviteFriends .main .mainTitle .qrCode a {
  font-size: 0.875rem;
  color: #fff;
  text-decoration: underline;
}
.inviteFriends .main .mainTitle .qrCode .qrCodeImg {
  display: inline-block;
  width: 0.9375rem;
  height: 0.9375rem;
  background-image: url(/img/qrCodeMin.png);
  background-repeat: no-repeat;
  background-size: 100% 100%;
  margin-right: 5px;
}
.inviteFriends .main .mainContent .inviteAssets {
  height: 5.3125rem;
  margin-top: 0.9375rem;
  margin-bottom: 0.9375rem;
  color: #fff;
  border-radius: 10px;
  background-color: #252736;
  display: flex;
  justify-content: space-around;
  align-items: center;
}
.inviteFriends .main .mainContent .inviteAssets .inviteNum {
  margin-top: 0.46875rem;
  font-size: 0.75rem;
}
.inviteFriends .main .mainContent .inviteAssets .num {
  color: #00D2FF;
  text-align: center;
}
.inviteFriends .main .am-tabs-default-bar {
  background-color: #13141c !important;
  color: #fff;
  position: relative;
}
.inviteFriends .main .am-tabs-default-bar:before {
  content: " ";
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  height: 1px;
  border-top: 1px solid #46464c;
  color: #46464c;
  transform-origin: 0 0;
  transform: scaleY(0.5);
}
.inviteFriends .main .currency-list {
  margin-top: 0.9375rem;
}
.inviteFriends .main .currency-list .list-top {
  height: 2.5rem;
  line-height: 2.5rem;
  font-family: 0.75rem;
  color: #ffffff;
  border-radius: 10px 10px 0px 0px;
  background: #1f2027 !important;
}
.inviteFriends .main .currency-list .list-item:nth-child(2n) {
  background: #1f2027;
}
.inviteFriends .main .currency-list .list-item:nth-child(2n-1) {
  background: #16181e;
}
.inviteFriends .main .currency-list .list-item {
  display: flex;
  flex: 4 1;
  text-align: center;
  height: 2.5rem;
  line-height: 2.5rem;
  font-family: 0.75rem;
  color: #ffffff;
}
.inviteFriends .main .currency-list .list-item div {
  flex: 1 1;
}
.inviteFriends .footer {
  position: fixed;
  bottom: 0px;
  left: 0px;
  bottom: 0;
  width: 100%;
  height: 6.25rem;
  display: flex;
  justify-content: space-around;
  align-items: center;
  background: linear-gradient(-45deg, #252736, #252736);
}
.inviteFriends .footer .leftMessage .am-input-item {
  box-sizing: border-box;
  width: 7.8125rem;
  height: 1.875rem;
  line-height: 1.875rem;
  padding-left: 0px;
  background: linear-gradient(-45deg, #2b2d37, #282b39);
  min-height: 28px;
}
.inviteFriends .footer .rightMessage .am-input-item {
  box-sizing: border-box;
  width: 7.8125rem;
  height: 1.875rem;
  line-height: 1.875rem;
  padding-left: 0px;
  background: linear-gradient(-45deg, #2b2d37, #282b39);
  min-height: 28px;
}
.inviteFriends .inviteFriendsButton {
  color: #fff;
}
.inviteFriends .inviteFriendsButton .am-list-item .am-list-line {
  padding-right: 0px;
}
.inviteFriends .inviteFriendsButton .input {
  position: relative;
}
.inviteFriends .inviteFriendsButton .input:after {
  content: " ";
  position: absolute;
  left: 0;
  top: 0;
  width: 200%;
  height: 1px;
  border: 1px solid #eee;
  color: #eee;
  height: 200%;
  transform-origin: left top;
  transform: scale(0.5);
  border-radius: 5;
}
.inviteFriends .inviteFriendsButton .input input {
  padding: 0px 5px;
  font-size: 0.5625rem;
  color: #fff;
  position: relative;
  z-index: 999;
}
.inviteFriends .inviteFriendsButton .button {
  margin-top: 0.625rem;
}
.inviteFriends .inviteFriendsButton .button button {
  border: none;
  background: linear-gradient(90deg, #1669f0, #00f7ff);
}
.inviteFriends .inviteFriendsButton .button .am-button {
  color: #fff;
  width: 7.8125rem;
  height: 1.875rem;
  font-size: 0.75rem;
  line-height: 1.875rem;
  background: linear-gradient(90deg, #1669f0, #00f7ff);
}
